<template>
    <div>
        <section class="shop_container">
            <nav class="goback" @click="goBack">
                <i class="el-icon-arrow-left"></i>
            </nav>
            <header class="shop_detail_header">
                <div class="header_cover_img_con">
                    <img :src="'https://elm.cangdu.org/img/'+shop.image_path" alt="" class="header_cover_img">
                </div>
                <section class="description_header">
                    <router-link to="">
                        <section class="description_left">
                            <img  :src="'https://elm.cangdu.org/img/'+shop.image_path" alt="">
                        </section>
                        <section class="description_right">
                            <h4 class="description_title ellipsis">{{shop.name}}</h4>
                            <p class="description_text">商家配送/分钟送达/配送费5元</p>
                            <p class="description_promotion ellipsis">
                                {{shop.promotion_info}}
                            </p>
                            <i class="el-icon-arrow-right"></i>
                        </section>
                    </router-link>
                </section>

            </header>
            <section class="change_show_type" v-if="show4">
                <div>
                    <span class="activity_show">商品</span>
                </div>
                <div>
                    <span @click=evaluateE()>评价</span>
                </div>
            </section>
            <section class="food_container" v-if="show4">
                <section class="menu_container">
                    <section class="menu_left">
                        <ul id="menu_left_ul" >
                            <li class="menu_left_li" v-for="(pro,index) in menu" :class="{bgc:index == current}" @click="chooseMenu(index)">
                                <span>{{pro.name}}</span>
                            </li>
                        </ul>
                    </section>
                    <section class="menu_right">
                        <ul style="right: 0" id="menu_right_ul">
                            <li class="" v-for="pro in getshopnum">
                                <header class="menu_detail_header">
                                    <section class="menu_detail_header_left">
                                    <strong class="menu_item_title">
                                        {{pro.name}}
                                    </strong>
                                        <span class="menu_item_description">
                                            {{pro.description}}
                                        </span>
                                    </section>
                                    <i class="el-icon-more"></i>
                                </header>
                                <section class="menu_detail_list" v-for="(proP,index) in pro.foods">
                                    <div class="menu_detail_link" @click="storeHomePage(proP,index)">
                                        <section class="menu_food_img">
                                            <img :src="'https://elm.cangdu.org/img/'+proP.image_path" alt="">
                                        </section>
                                        <section class="menu_food_description">
                                            <h3 class="food_description_head">
                                                <strong class="description_foodname">{{proP.name}}</strong>
                                                <ul class="attributes_ul" v-if="proP.attributes[0]==null?false:true">
                                                    <li class="color"  v-if="proP.attributes[0].icon_name=='招牌'?true:false">
                                                    <p style="color: rgb(240, 115, 115); " >招牌</p>
                                                    </li>
                                                    <li  v-else  class="attribute_new">
                                                        <p >新品</p>
                                                    </li>
                                                </ul>

                                            </h3>
                                            <p class="food_description_content">{{proP.description}}</p>
                                            <p class="food_description_sale_rating">
                                                <span>月售{{proP.month_sales}}份&nbsp</span>
                                                <span>&nbsp好评率{{proP.satisfy_rate}}%</span>
                                            </p>

                                            <p id="food_activity">
                                                <span v-if="proP.activity">{{proP.activity?proP.activity.image_text:null}}</span>
                                            </p>
                                        </section>
                                    </div>
                                    <footer class="menu_detail_footer">
                                    <section class="food_price">
                                        <span>￥</span>
                                        <span>{{proP.specfoods[0]?proP.specfoods[0].price:20}}</span>
                                        <span>起</span>
                                    </section>

                                        <section class="cart_module" >
                                            <section class="choose_specification" v-if="show1">
                                               <section class="choose_icon_container">
                                                   <span class="show_chooselist" v-if="proP.specfoods.length > 1" @click="chooseGG(proP)">选规格</span>
                                                   <i class="el-icon-circle-plus" v-if="proP.specfoods.length==1" @click="addcar(proP,0)"></i>
                                               </section>
                                                <transition name = "right">
                                                    <div class="eachnum" v-if="proP.mynum">
                                                        <span class="el-icon-remove-outline" @click="removecar(proP)"></span>
                                                        <span class="eachnum1">{{proP.mynum}}</span>
                                                    </div>
                                                </transition>
                                            </section>

                                        </section>
                                    </footer>
                                </section>
                            </li>
                        </ul>
                    </section>
                </section>
                <!--购物车-->
                <section class="buy_cart_container">
                    <section class="cart_icon_num">
                                <div class="cart_icon_container" id="cart_icon_activity" @click="show7 = !show7">
                                    <span id="cart_list_length" v-if="count">{{count}}</span>
                                    <i class="el-icon-shopping-cart-2"></i>
                                </div>
                        <div class="cart_num">
                            <div>￥{{price}}</div>
                            <div>配送费5元</div>
                        </div>
                    </section>
                    <section class="gotopay" id="gotopay_acitvity" v-if="!show5">
                        <span class="gotopay_button_style">还差20元起送</span>
                    </section>
                    <section class="gotopay" id="gotopay_acitvity1" v-else @click="goConfirmOrder()">
                        <span class="gotopay_button_style">结算</span>
                    </section>
                </section>
                <el-collapse-transition>
                    <div v-if="!show7" class="cart_food_list">
                        <header>
                            <h4>购物车</h4>
                            <div @click="deleteAll">
                                <i class="el-icon-delete"></i>
                                <span class="clear_cart">清空</span>
                            </div>
                        </header>
                        <section class="cart_food_details">
                            <ul>
                                <li class="cart_food_li" v-for="v in mycar" :key="v.name">
                                    <div class="cart_list_num">
                                        <p class="ellipsis">{{v.shop.name}}</p>
                                        <p class="ellipsis">{{v.shop.specfoods[v.i].specs_name}}</p>
                                    </div>
                                    <div class="cart_list_price">
                                        <span>￥</span>
                                        <span >{{v.shop.specfoods[v.i].price}}</span>
                                    </div>
                                    <section class="cart_list_control">
                                        <i class="el-icon-circle-plus" v-if="v.num" @click="addcar(v.shop,0)"></i>
                                        <transition name = "right">
                                            <div class="remove" v-if="v.num">
                                                <span class="el-icon-remove-outline" @click="removecar(v.shop)"></span>
                                                <span style="margin:0 .3rem">{{v.num}}</span>
                                            </div>
                                        </transition>

                                    </section>
                                </li>
                            </ul>
                        </section>
                    </div>
                </el-collapse-transition>
            </section>
            <!--评价页面-->
            <section class="change_show_type" v-if="!show4">
                <div>
                    <span @click="show4 = !show4" >商品</span>
                </div>
                <div>
                    <span class="activity_show">评价</span>
                </div>
            </section>
            <div class="rating_container"  v-if="!show4">
               <section style="overflow: scroll">
                   <section>
                       <header class="rating_header">
                           <section class="rating_header_left">
                               <p>{{shop.rating}}</p>
                                <p>综合评价</p>
                               <p>高于周边{{score}}%</p>
                           </section>
                            <section class="rating_header_right">
                                <p>
                                    <span>服务态度</span>
                                    <span class="rating_container start">
                                <el-rate
                                        v-model="service_score"
                                        disabled
                                        show-score
                                        text-color="#ff9900">
                                </el-rate>
                                 </span>
                                </p>
                                <p>
                                    <span>菜品评价</span>
                                    <span class="rating_container start">
                                <el-rate
                                        v-model="food_score"
                                        disabled
                                        show-score
                                        text-color="#ff9900">
                                </el-rate>
                                 </span>
                                </p>
                                <p>
                                    <span>送达时间</span>
                                    <span class="delivery_time">{{evaluateScore.deliver_time}}分钟</span>
                                </p>
                            </section>
                       </header>
                       <ul class="tag_list_ul">
                           <li v-for="(lis,index) in evaluateCf" :class="{tagActivity:index == current1,unsatisfied:index==2}" @click="evaluateCF(index)">{{lis.name}}({{lis.count}})</li>
                       </ul>
                       <ul class="rating_list_ul">
                            <li class="rating_list_li" v-for="evaluateLi in evaluate" :key="evaluateLi._id">
                                <img :src="'https://elm.cangdu.org/img/'+'default.jpg'" alt="" class="user_avatar">
                                <section class="rating_list_details">
                                    <header class="">
                                        <section class="username_star">
                                            <p class="username">{{evaluateLi.username}}</p>
                                            <p class="star_desc">
                                                <span class="rating_container start1">
                                                <el-rate
                                                        v-model="evaluateLi.rating_star"
                                                        disabled
                                                        text-color="#ff9900">
                                                </el-rate>
                                                </span>
                                                <span class="time_spent_desc">{{evaluateLi.time_spent_desc}}</span>
                                            </p>
                                        </section>
                                    </header>
                                    <ul class="food_img_ul">
                                        <li class="" v-for="pic in evaluateLi.item_ratings">
                                            <img :src="'https://fuss10.elemecdn.com/'+pic.image_hash" alt="">
                                        </li>
                                    </ul>
                                    <ul class="food_name_ul">
                                        <li class="ellipsis" v-for="name in evaluateLi.item_ratings">
                                            {{name.food_name}}
                                        </li>
                                    </ul>
                                </section>
                            </li>
                       </ul>
                   </section>
               </section>
            </div>
        </section>
        <!--选规格-->
        <section>
            <div class="specs_cover"></div>
            <div class="specs_list" v-if="!show6">
                <header class="specs_list_header">
                    <h4 class="ellipsis">{{nameGg.name}}</h4>
                    <span @click="show6=!show6">
                        <i class="el-icon-close"></i>
                    </span>

                </header>
                <section class="specs_details">
                    <h5 class="specs_details_title">{{nameGg.specifications[0].name}}</h5>
                    <ul>
                        <li v-for="(v,k) in specifications.values" :key="k" @click="choosefood(k,v)" :class="{specs_activity:current2==k}">{{v?v:null}}</li>
                    </ul>
                    <footer class="specs_footer">
                        <div class="specs_price">
                            <span>￥</span>
                            <span>{{specfoods[current2]?specfoods[current2].price:20}}</span>
                        </div>
                        <div class="specs_addto_cart" @click="addcar(nameGg,current2);">加入购物车</div>
                    </footer>
                </section>
            </div>
        </section>

    </div>
</template>

<script>
    import $ from 'jquery'
    // import  from '../store/index'
    import ElCollapseTransition from "element-ui/src/transitions/collapse-transition";
    export default {
        name: "Shop",
        components: {ElCollapseTransition},
        data(){
            return{
                shopid:"",
                shop:[],
                menu:[],
                current:0,
                current1:0,
                current2:0,
                show1:true,
                show2:true,
                show3:false,
                show4:true,
                show5:true,
                show6:true,
                show7:true, show8:true,
                num:0,
                mycar:"",
                count:0,
                foodinfo:"",
                evaluate:[],
                evaluateScore:"",
                score:0,
                service_score:0,
                food_score:0,
                rating_star:0,
                evaluateCf:[],
                price:0,
                nameGg:"",
                specifications:[],
                specfoods:[],
                index:0,
                geohash:"",
                entities:[],
            }
        },
        created(){
            this.shopid = this.$route.query.shopid
            this.geohash=this.$route.query.geohash,
            console.log(this.geohash)
            this.axios.get("shopping/restaurant/"+this.shopid,{
            }).then(res=>{
                this.shop = res
                console.log(res)
            }).catch(err=>{})
            this.axios.get("shopping/v2/menu?restaurant_id="+this.shopid,{
            }).then(res=>{
                this.foodinfo = res
                this.menu = res;
                console.log(res)
            }).catch(err=>{})
            if(localStorage.getItem("mycar")){
                this.mycar = JSON.parse(localStorage.getItem("mycar"))
            }
            this.cartCount();

        },
        methods:{
            evaluateCF(i){
              this.current1 = i;
            },
            goBack(){
              this.$router.go(-1)
            },
            chooseMenu(index){
                this.current = index;
                console.log(index);
                if (index==0){
                    $("#menu_right_ul").css("marginTop","0px");
                }
                if (index==1){
                    $("#menu_right_ul").css("marginTop","-6000px");
                }
                if (index==2){
                    $("#menu_right_ul").css("marginTop","-9700px");
                }
                if (index==3){
                    $("#menu_right_ul").css("marginTop","-14133px");
                }
                if (index==4){
                    $("#menu_right_ul").css("marginTop","-15967px");
                }
                if (index==5){
                    $("#menu_right_ul").css("marginTop","-17217px");
                }
                if (index==6){
                    $("#menu_right_ul").css("marginTop","-17525px");
                }
            },
            evaluateE(){
                this.show4 = !this.show4;
                //获取评价信息
                this.axios.get("ugc/v2/restaurants/"+this.shopid+"/ratings",{
                }).then(res=>{
                    this.evaluate = res;
                    // this.score = ((this.evaluate.rating_star)).toFixed(1);
                    console.log(res)
                }).catch(err=>{})
                //获取评价分数
                this.axios.get("ugc/v2/restaurants/"+this.shopid+"/ratings/scores",{
                }).then(res=>{
                    this.evaluateScore = res
                    // console.log(res)
                    this.score = (((this.evaluateScore.compare_rating)*100).toFixed(1))/1;
                    this.service_score =((this.evaluateScore.service_score).toFixed(1))/1;
                    this.food_score =((this.evaluateScore.food_score).toFixed(1))/1;
                    // console.log(this.food_score)
                }).catch(err=>{})
                //获取评价分类
                this.axios.get("ugc/v2/restaurants/"+this.shopid+"/ratings/tags",{
                }).then(res=>{
                    this.evaluateCf = res
                    console.log(res)
                }).catch(err=>{})



            },
            //加入购物车，存入localstorage
            addcar(e,index){

                this.index=index;
                // console.log(this.index);
                this.show6 = true;
                // console.log(e);
                if(localStorage.getItem("mycar")){
                    let mycar = JSON.parse(localStorage.getItem("mycar"));
                    let addok = true;
                    for(var i =0; i < mycar.length;i++){
                        if(mycar[i].shop.item_id == e.item_id && mycar[i].shop.category_id == e.category_id && mycar[i].shop.restaurant_id == e.restaurant_id){
                            mycar[i].num = mycar[i].num + 1;
                            mycar[i].i = index;
                            addok = false;
                            break;
                        }
                    }
                    if(addok){
                        mycar.push({"shop":e,"num":1,"i":index})
                    }
                    this.mycar = mycar;
                    localStorage.setItem("mycar",JSON.stringify(mycar));
                }else{
                    var mycar = [{"shop":e,"num":1,"i":index}]
                    this.mycar = mycar;
                    localStorage.setItem("mycar",JSON.stringify(mycar))
                }

                this.cartCount();

            },
            //删除
            removecar(e){
                if (this.count == 0){
                    $("#cart_list_length").css({
                        "display":"none",
                        "backgroundColor":"transparent"
                    })
                }
                for(var i = 0;i < this.mycar.length;i++){
                    if(this.mycar[i].shop.specfoods[0]._id == e.specfoods[0]._id){
                        this.mycar[i].num == 1 ? this.mycar.splice(i,1) : (this.mycar[i].num = this.mycar[i].num - 1);
                        // this.mycar[i].index == 1 ? this.mycar.splice(i,1)
                        break;
                    }
                };
                localStorage.setItem("mycar",JSON.stringify(this.mycar))
                this.cartCount();
            },
            //加入购物车总数量,价钱
            cartCount(i){
              // console.log(this.mycar);
                  this.count = 0;
                  this.price = 0;
                  for (let i = 0; i <this.mycar.length;i++){
                      let index = this.mycar[i].i
                      this.count += this.mycar[i].num;
                      this.price += (this.mycar[i].shop.specfoods[index].price*this.mycar[i].num);
                      // console.log(this.price);
                      // console.log(this.count)
                  }
                  if (this.mycar.length==0){
                      this.show7 = true;
                      this.show5 = false;
                  }
                if (this.count){
                    $("#gotopay_acitvity").css("backgroundColor","#4cd964")
                    $("#gotopay_acitvity1").css("backgroundColor","#4cd964")
                    $("#cart_icon_activity").css("backgroundColor","#3190e8")
                    this.show5 = true;
                }
                if(this.mycar.length == 0){
                    this.show5 = false;
                    $("#gotopay_acitvity").css("backgroundColor","#535356")
                    $("#gotopay_acitvity1").css("backgroundColor","#535356")
                    $("#cart_icon_activity").css("backgroundColor","#535356")
                }
            },
            chooseGG(item){
                this.nameGg = item
                // console.log(item);
                this.specifications = item.specifications[0];
                this.show6 = false;
                this.specfoods = item.specfoods;
            },
            choosefood(k,v){
                this.current2 = k;
                console.log(v);
            },
            deleteAll(){
                if(localStorage.getItem("mycar")){
                    this.mycar = JSON.parse(localStorage.getItem("mycar"))
                }
                localStorage.removeItem('mycar');
                this.show7 = true;
                this.count=0;
                this.price=0.00;
                this.mycar.length=0;
                if(this.mycar.length == 0){
                    this.show5 = false;
                    $("#gotopay_acitvity").css("backgroundColor","#535356")
                    $("#gotopay_acitvity1").css("backgroundColor","#535356")
                    $("#cart_icon_activity").css("backgroundColor","#535356")
                }
            },
            storeHomePage(item,index){
                console.log(item,index);
                this.$router.push({
                    path:"/fooddetail",
                    query:{
                        shop:item,
                    }
                })
            },
            goConfirmOrder(){
                let j = 0;
                for(let i=0;i < this.mycar.length;i++){
                    let obj = {
                        attrs:this.mycar[i].shop.attrs,
                        extra:{},
                        id:this.mycar[i].shop.specfoods[j].food_id,
                        name:this.mycar[i].shop.name,
                        packing_fee:this.mycar[i].shop.specfoods[j].packing_fee,
                        price:this.mycar[i].shop.specfoods[j].price,
                        quantity:this.mycar[i].num,
                        sku_id:this.mycar[i].shop.specfoods[j].sku_id,
                        specs:this.mycar[i].shop.specfoods[j].specs,
                        stock:this.mycar[i].shop.specfoods[j].stock,
                    }
                    this.entities.push(obj);
                }
                console.log(this.entities);
                this.$store.commit("cart",this.shop);
                this.$router.push({
                    path:"/confirmOrder",
                    query:{
                        shop:this.shop,
                        geohash:this.geohash,
                        entities:this.entities
                    }
                })
            }
        },
        computed:{
            getshopnum(){
                for(let i = 0; i < this.menu.length; i++){
                    let thisnum = 0;
                    for(let k = 0; k < this.menu[i].foods.length;k++){
                        var isadd = true;
                        // 判断商品是否在购物车
                        for(let j = 0;j < this.mycar.length;j++){
                            if(this.menu[i].foods[k].specfoods[0]._id == this.mycar[j].shop.specfoods[0]._id){
                                this.menu[i].foods[k].mynum = this.mycar[j].num;
                                thisnum += this.mycar[j].num;
                                isadd = false;
                            }
                        }
                        if(isadd){
                            this.menu[i].foods[k].mynum = 0;
                        }
                    }
                    this.menu[i].mynum = thisnum;
                }
                return this.menu;
            },


        }
    }
</script>

<style scoped>
    .rating_container{
        flex: 1;
        overflow-y: hidden;
        flex-direction: column;
        width: 100%;
    }
    .rating_header{
        display: flex;
        background-color: #fff;
        padding: .8rem .5rem;
        margin-bottom: .5rem;
    }
    .rating_header_left{
        flex: 3;
        text-align: center;
    }
    .rating_header_right{
        flex: 4;
    }
    .rating_header .rating_header_left p:first-of-type {
        font-size: 1.2rem;
        color: #f60;
    }
    .rating_header_left p:nth-of-type(2) {
        font-size: .65rem;
        color: #666;
        margin-bottom: .1rem;
    }
    .rating_header_left p:nth-of-type(3) {
        font-size: .4rem;
        color: #999;
    }
    .rating_header_right p{
        font-size: .65rem;
        line-height: 1rem;
        display: flex;
        align-items: center;
        justify-content: flex-start;
    }
    .rating_header_right p span:first-of-type {
        color: #666;
        margin-right: .5rem;
    }
    .shop_container{
        display: flex;
        flex-direction: column;
        position: absolute;
        right: 0;
        left: 0;
        height: 100%;
        overflow: hidden;
    }
    .start1  /deep/ .el-rate__item{
        /*margin-right: -.4rem;*/
        /*width: .5rem;*/
    }
    .start{
        display: inline-block;
        height: 1rem;
    }
    .start /deep/ .el-rate {
        width: 3rem;
        padding-top: .39rem;
    }
    .start1{
        display: inline-block;
        height: 1rem;
    }
    .start1 /deep/ .el-rate {
        width: 2rem;
        padding-top: .39rem;
    }
    .rating_header_right p .delivery_time {
        font-size: .4rem;
        color: #999;
    }
    .tag_list_ul {
        display: flex;
        flex-wrap: wrap;
        background-color: #fff;
        padding: .5rem;
    }
    .tag_list_ul li{
        font-size: .6rem;
        color: #6d7885;
        padding: .3rem;
        background-color: #ebf5ff;
        border-radius: .2rem;
        border: 1px;
        margin: 0 .4rem .2rem 0;
    }
    .unsatisfied{
        background-color: #f5f5f5 !important;
        color: #aaa !important;
    }
    .tagActivity{
        background-color: #3190e8 !important;
        color: #fff !important;
    }
    .rating_list_ul{
        background-color: #fff;
        padding: 0 .5rem;
        overflow: scroll;
    }
    .rating_list_li{
        border-top: 1px solid #f1f1f1;
        display: flex;
        padding: .6rem 0;
    }
    .rating_list_li .user_avatar {
        width: 1.5rem;
        height: 1.5rem;
        border: .025rem;
        border-radius: 50%;
        margin-right: .8rem;
